.list {
  padding-left: 0;
  list-style: none;
  padding: 20px;
}

.list__element {
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  padding: 10px;
  border-bottom: 2px solid rgb(231, 230, 230);
  gap: 10px;
}

.list__element--hidden {
  display: none;
}

.list__item {
  word-break: break-word;
}

.list__item--done {
  text-decoration: line-through;
}

.list__button {
  width: 30px;
  height: 30px;
  color: white;
  background-color: hsl(120, 100%, 25%);
  border: none;
  text-align: center;
  transition: 1s;
}

.list__button:hover {
  background-color: hsl(120, 100%, 35%);
}

.list__button--remove {
  background-color: hsl(0, 100%, 50%);
}

.list__button--remove:hover {
  background-color: hsl(0, 100%, 70%);
}
