.form {
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 20px;
}

.form__input {
  flex-grow: 1;
  padding: 10px;
  border-color: 2px solid rgb(231, 230, 230);
}

@media (max-width: 767px) {
  .form {
    display: grid;
    grid-template-columns: 1fr;
  }
}

.form__button {
  color: white;
  background-color: teal;
  border: none;
  width: fit-content;
  padding: 10px;
  transition: 1s;
}

.form__button:hover {
  background-color: rgba(0, 128, 128, 0.741);
  transform: scale(1.1);
  cursor: pointer;
}

@media (max-width: 767px) {
  .form__button {
    width: 100%;
  }
}
